<template>
  <div>
    <div class="dashboard-container">
      <div class="dashboard-wrapper">
        <nav class="nav-column mt-5">
          <div class="title-wrapper">
            <div class="logo ml-4"></div>
            <h1 class="title ml-4">Dashboard</h1>
          </div>
          <div class="description-wrapper">
            <p class="description m-0">Sherman Oaks Medical Supplies</p>
            <button class="hint ml-2 mr-4"></button>
          </div>
        </nav>
        <main class="main pt-5">
          <aside class="menu-wrapper ml-3">
            <div class="menu">
              <h2 class="floor-title mt-2">Floor</h2>
              <a class="floor mt-1" href="#" @click.prevent>16</a>
              <a class="floor my-3" href="#" @click.prevent>18</a>
            </div>
            <button class="logout mb-3 ml-1"></button>
          </aside>
          <section class="main-content">
            <div class="patient mx-2 mb-3" v-for="item in breathsDataAll" :key="item.id"
              @click="enterDetail(item.userID)">
              <div class="info-wrapper mt-3">
                <div class="num ml-3">{{item.userID}}</div>
                <div class="info-content ml-2">
                  <span class="name">Frederick Bradley</span>
                  <span class="status">Reposition 1:24</span>
                </div>
              </div>
              <div class="mt-3 w-100 chart-content">
                <canvas :id="'chart-'+item.userID" width="100%" class="chart"></canvas>
              </div>
            </div>
            <div class="add-patient mx-3">
              <button class="btn-addpatient"></button>
            </div>
          </section>
        </main>
      </div>
    </div>
  </div>
</template>

<script>
import api from '../api';
import Chart from 'chart.js';

export default {
  data() {
    return {
      admin: { // admin 資訊
        adminView: null,
      },
      patientGroup: { // 病患群組
        all: [],
        group1: [],
        group2: [],
      },
      nowTime: Math.floor(new Date().getTime() / 1000), // 現在時間
      breathsDataAll: [], // 呼吸 api 資料
      isCors: this.$store.state.isCors, // 是否跨域
      reGetRealTime: null, // 持續更新及時圖表
      charts: [],
      breathsData: { // 心跳資料
        labels: [],
        value: [],
      },
    }
  },
  computed: {
    token() {
      return JSON.parse(localStorage.getItem('token')).token;
    },
  },
  methods: {
    enterDetail(userID) { // 進入 Detail
      const vm = this;
      localStorage.setItem('userID', JSON.stringify({ userID: userID }));
      vm.$store.dispatch('loadingOverlay');
      setTimeout(() => {
        vm.$store.dispatch('loadingOverlay');
        vm.$router.push({ path: `detail/${userID}` });
      }, 750);
    },
    getPatinetsInfo(startTimestamp, fetchTime) { // 折線圖 api
      const vm = this;
      let endTimestamp = startTimestamp + fetchTime;
      return api
      .breathsDataAll(
        vm.isCors,
        startTimestamp,
        endTimestamp,
        vm.token
      )
      .then(res => res.json())
      .then(resjson => {
        console.log(resjson.data)
        vm.breathsDataAll = resjson.data;
        /*----- dummy data -----*/
        vm.breathsDataAll = [
            {
                "value": 0,
                "time": "2019-09-28T17:05:01.833896Z",
                "userID": "11"
            },
            {
                "value": 0,
                "time": "2019-09-20T03:56:38.567222Z",
                "userID": "12"
            },
            {
                "value": 1,
                "time": "2019-10-16T06:03:06.576202Z",
                "userID": "13"
            },
            {
                "value": 1,
                "time": "2019-10-16T06:03:06.576202Z",
                "userID": "14"
            },
            {
                "value": 1,
                "time": "2019-10-16T06:03:06.576202Z",
                "userID": "15"
            },
            {
                "value": 1,
                "time": "2019-10-16T06:03:06.576202Z",
                "userID": "16"
            },
            {
                "value": 1,
                "time": "2019-10-16T06:03:06.576202Z",
                "userID": "17"
            },
            {
                "value": 1,
                "time": "2019-10-16T06:03:06.576202Z",
                "userID": "18"
            },
            {
                "value": 1,
                "time": "2019-10-16T06:03:06.576202Z",
                "userID": "19"
            },
        ]

        vm.setAdminView();
      });
    },

    getAdminInfo() { // 取得 admin 視角
      const vm = this;
      // vm.admin.adminView = JSON.parse(localStorage.getItem('adminView')).adminView;
      vm.admin.adminView = 'all';
    },

    setAdminView() { // admin 視角設定
      const vm = this;
      if (vm.admin.adminView === 'group1') {
        vm.patients = [...vm.patientGroup.group1];
      } else if (vm.admin.adminView === 'group2') {
        vm.patients = [...vm.patientGroup.group2];
      } else if (vm.admin.adminView === 'all') {
        vm.patients = vm.patientGroup.group1;
      }
    },

    updateChart() { // 更新 chart
      const vm = this;
      vm.reGetRealTime = setInterval(() => {
        vm.getPatinetsInfo(vm.nowTime, 1);
        vm.nowTime ++;
      }, 1000);
      // console.log('charts', vm.charts);
    },

    initChart() {
      const vm = this;
      let ctx = document.getElementsByClassName('chart');
      [ ...ctx ].forEach(el => {
        vm.charts[el.id] = new Chart(el, {
          labels: vm.breathsData.labels, // x軸標籤
          type: 'line',
          data: {
            labels: '', // 圖表標題的標題
            datasets: [
              {
                label: '', // 圖表標題的圖標
                radius: 0, // 隱藏圓點
                data: [], // data
                borderColor: 'rgb(78, 178, 211)', // 線的顏色
                fill: false, // 是否填滿
              },
            ],
          },
          responsive: false, // 自適應
          options: {
            legend: {
              display: false,  // 隱藏背後的格子
            },
            tooltips: {
              intersect: false, // 不一定要指到點才能顯示標籤
            },
            scales: {
              xAxes: [
                {
                  gridLines: false, // 隱藏背後的格子
                  afterFit: (scale) => { // x軸高度
                    scale.height = '7';
                  }
                }
              ],
              yAxes: [
                {
                  ticks: {
                    beginAtZero: false, // 是否從 0 開始
                    suggestedMin: 0, // y軸下限
                    suggestedMax: 100, // y軸上限
                    display: false, // 是否y軸顯示
                  },
                  scaleLabel: {
                    display: true, // 是否顯示數值
                    labelString: '', // 標籤文字
                  },
                  gridLines: false, // 隱藏背後的格子
                },
              ],
            },
          },
        });
      });
    },

    setChart() { // 設定折線圖
      // console.log('setChart')
      const vm = this;
      vm.breathsDataAll.forEach(item => {
        Object.keys(vm.charts).forEach(function(key) {
          // console.log(key, vm.charts[key]);
          // console.log(item);
          if ('chart-'+item.userID === key) {
            let chart = vm.charts[key];
            chart.data.datasets[0].data.push(Math.floor(Math.random()*(80-30+1))+30) // 假的假的假的;
            chart.data.labels.push('00:00'); // 假的假的假的
            if (chart.data.labels.length > 5) { // 超過 5 筆資料後移除先前的
              chart.data.labels.shift(); // 移除圖表的第一筆標籤
              chart.data.datasets[0].data.shift(); // 移除圖表的第一筆資料
            }
            chart.update(); // 更新呼吸圖表
            return;
          }
        });
      });
    },
  },
  created() { // 進入時取得使用者視角
    this.$store.dispatch('loadingOverlay');
    this.setAdminView();
    this.getAdminInfo();
  },
  beforeDestroy() { // 關掉時停止持續取資料
    const chartCanvas = document.querySelectorAll('.chart');
    clearInterval(this.reGetRealTime);
  },
  mounted() { // 取得病患資訊
    this.updateChart();
    setTimeout(() => { // 讓畫面load好後再呈現
      this.$store.dispatch('loadingOverlay');
    }, 2000)
  },
  updated() {
    const vm = this;
    if (Object.keys(vm.charts).length == 0) {
      vm.initChart();
    } else if (Object.keys(vm.charts).length > 0) {
      vm.setChart();
    }
  }
};
</script>

<style lang="scss" scope>
* {
    font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  }

  button {
    border: 0;
    &:focus {
      border: 0;
      outline: 0;
    }
  }

  .dashboard-container {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .dashboard-wrapper {
    width: 1024px;
    height: 768px;
    background: rgb(247, 249, 254);
  }

  .nav-column {
    height: 75px;
    width: 100%;
    display: flex;
    justify-content: space-between;
  }

  .title-wrapper {
    display: flex;
    align-items: center;
  }

  .logo {
    width: 75px;
    height: 75px;
    background: url('~@/assets/images/Studio1Labs_Logo_Full.png') center center / 100% no-repeat;
  }

  .title {
    color: #404047;
    font-size: 36px;
    font-weight: bold;
  }

  .description-wrapper {
    display: flex;
    align-items: center;
  }

  .description {
    font-size: 16px;
    color: #00263a;
  }

  .hint {
    width: 32px;
    height: 32px;
    background: #00263a url('~@/assets/images/ic-notifications.svg') center center / 75% no-repeat;
    display: inline-block;
    border-radius: 10px;
    &:active {
      background-color: darken(#00263a, 10%);
    }
  }

  .menu-wrapper{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .menu {
    height: 180px;
    width: 82px;
    background: #00263a;
    border-radius: 16px;
    box-shadow: 2px 4px 8px 0 rgba(0, 38, 58, 0.3);
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .logout {
    width: 32px;
    height: 32px;
    border-radius: 10px;
    background: #00263a url('~@/assets/images/log-out.svg') center center / 75% no-repeat;
    &:active {
      background-color: darken(#00263a, 10%);
    }
  }

  .floor-title {
    font-size: 20px;
    color: #fff;
    font-weight: lighter;
  }

  .floor {
    width: 50px;
    height: 50px;
    font-weight: bold;
    color: #fff;
    background-color: rgba(255, 255, 255, 0.3);
    font-size: 30px;
    line-height: 50px;
    text-align: center;
    border-radius: 8px;
    &:hover {
      text-decoration: none;
      color: white;
    }
  }

  .main {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
  }

  .main-content {
    width: 918px;
    height: 588px;
    display: flex;
    flex-wrap: wrap;
    overflow: scroll;
  }

  .patient {
    width: 274px;
    height: 180px;
    border-radius: 16px;
    background: #fff;
    box-shadow: 0 2px 4px 0 rgba(64, 64, 71, 0.3);
    cursor: pointer;
  }

  .info-wrapper {
    width: 100%;
