  }

  .status {
    font-size: 14px;
  }

  .chart-content {
    width: 100%;
    height: 80px;
    position: relative;
  }

  .add-patient {
    width: 274px;
    height: 180px;
    border-radius: 16px;
    box-shadow: inset 0 0 4px 0 rgba(64, 64, 71, 0.3);
    background-color: rgba(255, 255, 255, 0.4);
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .btn-addpatient {
    width: 56px;
    height: 56px;
    background: #197890 url('~@/assets/images/ic-add.png') center center / 40% no-repeat;
    border-radius: 50%;
    box-shadow: 2px 4px 8px 0 rgba(25, 120, 144, 0.3);
    &:active {
      background-color: darken(#197890, 10%);
    }
  }
</style>
